import React from 'react';
import './App.scss';

const goodsFromServer: string[] = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

type State = {
  goods: string[];
  isVisible: boolean;
};

export class App extends React.Component<{}, State> {
  state = {
    goods: goodsFromServer,
    isVisible: false,
  };

  showList = () => {
    this.setState((state) => ({
      isVisible: !state.isVisible,
    }));
  };

  getReverseGoodsList = () => {
    this.setState((state) => ({
      // goods: [...state.goods].reverse(),
      goods: state.goods.reverse(),
    }));
  };

  getSortAlphabetGoodsList = () => {
    this.setState((state) => ({
      // goods: [...state.goods].sort((a, b) => a.localeCompare(b)),
      goods: state.goods.sort((a, b) => a.localeCompare(b)),
    }));
  };

  getSortLengthGoodsList = () => {
    this.setState((state) => ({
      // goods: [...state.goods].sort((a, b) => a.length - b.length),
      goods: state.goods.sort((a, b) => a.length - b.length),
    }));
  };

  getResetGoodsList = () => {
    this.setState({
      goods: goodsFromServer,
    });
  };

  render() {
    const { isVisible } = this.state;

    return (
      <div className="App">
        <h1>Goods</h1>

        {!isVisible
          ? (
            <button
              type="button"
              onClick={this.showList}
            >
              Start
            </button>
          ) : (
            <ul className="list list__wrapper">
              {goodsFromServer.map(good => (
                <li className="list__item" key={good}>
                  {good}
                </li>
              ))}

              <button
                type="button"
                onClick={this.getReverseGoodsList}
              >
                Reverse
              </button>

              <button
                type="button"
                onClick={this.getSortAlphabetGoodsList}
              >
                Sort alphabetically
              </button>

              <button
                type="button"
                onClick={this.getSortLengthGoodsList}
              >
                Sort by length
              </button>

              <button
                type="button"
                onClick={this.getResetGoodsList}
              >
                Reset
              </button>

              <button
                type="button"
                onClick={this.showList}
              >
                Hide List
              </button>
            </ul>
          )}
      </div>
    );
  }
}
